
import { defineComponent } from "vue";

interface RegexCard {
  Before: string;
  After: string;
  Id: string;
}

function getUniqueStr(): string {
  var strong = 1000;
  return (
    new Date().getTime().toString(16) +
    Math.floor(strong * Math.random()).toString(16)
  );
}

export default defineComponent({
  name: "App",
  components: {},
  data: () => {
    return {
      cards: [] as RegexCard[],
      before: "",
      after: "",
    };
  },
  methods: {
    addCard: function () {
      this.$data.cards.push({
        Before: "",
        After: "",
        Id: getUniqueStr(),
      });
    },
    deleteCard: function (id: string) {
      const idx = this.$data.cards.findIndex((value) => {
        return value.Id === id;
      });
      this.$data.cards.splice(idx, 1);
    },
    trans: function () {
      const tmp = this.$data.cards;
      tmp.sort((a, b) => {
        const rect1 = document.getElementById(a.Id)?.getBoundingClientRect();
        const rect2 = document.getElementById(b.Id)?.getBoundingClientRect();
        if (!rect1 || !rect2) {
          return 1;
        }
        return rect1.top > rect2.top ? 1 : -1;
      });
      this.$data.cards.forEach((value) => {
        const reg = new RegExp(value.Before, "g");
        this.$data.after = this.$data.before.replace(reg, value.After);
      });
    },
  },
});
